import navigation from '../../components/navigation/navigation';
export default {
	data() {
		return {
			array: [ '女', '男',],
			index: 1,
			config: {
				type: 0, //0无title，1有title
				title: '个人资料',
				back: true
			},
			userInfo: {
				name: '',
				image: '',
				phone: '',
				sex: '',
				profile: ''
			},
			userId:'',
			visible:false,
			value:'',
			type:0,
		}
	},
	components: {
		navigation,
	},
	onLoad() {
		if (uni.getStorageSync('userId')) {
			this.userId = uni.getStorageSync('userId')
			this.getuser()
		} else {
			this.customConduct()
		}
	},
	onShow(){
		this.getuser()
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		close(){
			this.visible = false
			this.value = ''
		},
		modifyName() {
			this.type = 0
			this.visible = true
			this.value = ''
			// uni.navigateTo({
			// 	url: '../my/editMineDetail'
			// })
		},
		modifyProfile() {
			this.type = 1
			this.visible = true
			this.value = ''
			// uni.navigateTo({
			// 	url: '../my/editMineProfile'
			// })
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = Number(e.target.value)
			this.setSex()
		},
		getuser(){
			this.$http.get('/api/person/info/', {
				userId: this.userId
			}).then(res => {
				this.userInfo = res
				this.index = res.sex
			}).catch((e) => {});
		},
		setSex(){
			let _this = this
			this.$http.post('/api/person/update/', {
				userId: this.userId,
				name: this.userInfo.name,
				sex: this.index,
				introduce: this.userInfo.introduce,
				avatarUrl: this.userInfo.avatarUrl,
			}).then(res => {
				uni.showToast({
					title: '修改成功',
					icon: 'none'
				})
				_this.getuser()
			}).catch((e) => {});
		},
		getImage() {
			uni.chooseImage({
				count: 1,
				success: (res) => {
					console.log(res)
					let url = res.tempFilePaths[0]
					this.uploadimage(url)
				},
				fail: (err) => {
					console.log(err)
				}
			});
		},
		uploadimage(url){
			let upload_task = uni.uploadFile({
				url: 'https://tjuyjn.top:1314/api/publish/image',
				filePath: url,
				name: 'image',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					console.log(JSON.parse(res.data))
					let data = JSON.parse(res.data)
					if (data.result == 1) {
						this.setavatar(data.url);
					} else {
						uni.showToast({
							title: data.message,
							icon: 'none'
						})
					}
			
				}
			})
			upload_task.onProgressUpdate((res) => {});
		},
		setavatar(e){
			let _this = this
			this.$http.post('/api/person/update/', {
				userId: this.userId,
				name: this.userInfo.name,
				sex: this.userInfo.sex,
				introduce: this.userInfo.introduce,
				avatarUrl: e,
			}).then(res => {
				uni.showToast({
					title: '修改成功',
					icon: 'none'
				})
				_this.getuser()
			}).catch((e) => {});
		},
		handleset(){
			let _this = this
			if (this.type == 0){
				this.$http.post('/api/person/update/', {
					userId: this.userId,
					name: this.value,
					sex: this.userInfo.sex,
					introduce: this.userInfo.introduce,
					avatarUrl: this.userInfo.avatarUrl,
				}).then(res => {
					uni.showToast({
						title: '修改成功',
						icon: 'none'
					})
					_this.visible = false
					_this.getuser()
				}).catch((e) => {});
			} else {
				this.$http.post('/api/person/update/', {
					userId: this.userId,
					name: this.userInfo.name,
					sex: this.userInfo.sex,
					introduce: this.value,
					avatarUrl: this.userInfo.avatarUrl,
				}).then(res => {
					uni.showToast({
						title: '修改成功',
						icon: 'none'
					})
					_this.visible = false
					_this.getuser()
				}).catch((e) => {});
			}
		}
	}
}
